import React from 'react'
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { Nav, Navbar } from 'react-bootstrap'
import { Link, useLocation } from 'react-router-dom';
import styled from 'styled-components';

const NavBar = styled(Navbar)`
  backdrop-filter: blur(20px);
`;

/**
 * @author Alex Andrei
 * @description Get the current path
 * @returns Current path
 */
const usePathname = () => {
  const location = useLocation();
  return location.pathname;
}

export const Navigation = () => (
  <NavBar className='justify-content-between px-3' fixed="top">
    <Link to='/'>
      <img alt="Logotipo Alex Andrei" src={require("../../../assets/icons/logo-andrei.svg").default} />
    </Link>
    {
      usePathname() !== "/"
        ? <Link className='text-dark px-2' to='/'>Home</Link>
        :
        <Nav>
          <AnchorLink className='text-dark px-2' href='#home' offset='100'>Home</AnchorLink>
          <AnchorLink className='text-dark px-2' href='#design' offset='280'>Design</AnchorLink>
          <AnchorLink className='text-dark px-2' href='#favoriteStack'>Stack</AnchorLink>
        </Nav>
    }
  </NavBar>)