import React from 'react'
import { Button, Col, Container, Row } from 'react-bootstrap'
import styled from 'styled-components'
import Image from '../../atoms/image'
import Fade from 'react-reveal/Fade';

import assetOne from '../../../assets/patterns/Group.svg'

const ContainerMain = styled(Container)`
  &::before {
    background: url('${assetOne}') no-repeat;
    bottom: 15rem;
    content: '';
    height: 299px;
    left: 0;
    position: absolute;
    width: 100%;
  }
`

export const Portfolio = () => (
  <div className='bg-white py-5 position-relative'>
    <ContainerMain className='position-relative'>
      <h2>Portfolio</h2>
      <Fade cascade delay={800} duration={2000}>
        <Row>
          <Col md={4}>
            <Image
              alt=''
              classess='d-block image-profile img-fluid mx-auto mt-3 mb-4 position-relative'
              source={require('../../../assets/images/Doco website iPhone.png').default}
            />
            <a href='https://www.do-co.mx/' rel='noreferrer' target='_blank' title='Go to DoCo'><Button className='w-100' variant='outline-primary'>Go to website</Button></a>
          </Col>
          <Col md={4}>
            <Image
              alt=''
              classess='d-block image-profile img-fluid mx-auto mt-3 mb-4 position-relative'
              source={require('../../../assets/images/Retos Jalisco website iPhone.png').default}
            />
            <a href='https://retos.jalisco.gob.mx/' rel='noreferrer' target='_blank' title='Go to Retos Jalisco'><Button className='w-100' variant='outline-primary'>Go to website</Button></a>
          </Col>
          <Col md={4}>
            <Image
              alt=''
              classess='d-block image-profile img-fluid mx-auto mt-3 mb-4 position-relative'
              source={require('../../../assets/images/UX Creativity website iPhone.png').default}
            />
            <a href='http://uxcreativity.com/' rel='noreferrer' target='_blank' title='Go to UX Creativity'><Button className='w-100' variant='outline-primary'>Go to website</Button></a>
          </Col>
        </Row>
      </Fade>
    </ContainerMain>
  </div>
)